import React, { useState } from 'react'
import s from './Content.module.css'
import WinnersBlock from './WinnersBlock/WinnersBlock';

function Content(props) {

    let Data = [
        [
            [
                ['Скала Джонсон Автомобиль', 'БГУИР'], 
                ['Жак Фреско СПИПов', 'БГУИР'],
                ['Какой-то ИиТПшник КСИС', 'БГУИР']
            ],
        ],

        [
            [
                ['Genii Progi OH', 'БГУ'], 
                ['SMTH IN THE', 'БГУ'],
                ['Сколько ещё верстать?', 'БГУ']
            ],
        ],

        [
            [
                ['Лень придумывать Имя', 'Не БНТУ'], 
                ['Трухан Егор Валерьевич', 'Не БНТУ'],
                ['Это я сверху', 'Не БНТУ']
            ],
        ],

        [
            [
                ['Какойто умный кент', 'Хогварц'], 
                ['Этот тоже не глупый', 'Хогварц'],
                ['НУ вот и всё', 'Хогварц']
            ],
        ],

    ];

    let [SelectedItem, SetSelectedItem] = useState(0);

    return (
        <div className={s.Content}>
            <div className={s.paginator}>
                <div onClick={ () => SetSelectedItem(0) } className={SelectedItem === 0 ? s.itemCoosing : s.item}>2021</div>
                <div onClick={ () => SetSelectedItem(1) } className={SelectedItem === 1 ? s.itemCoosing : s.item}>2020</div>
                <div onClick={ () => SetSelectedItem(2) } className={SelectedItem === 2 ? s.itemCoosing : s.item}>2019</div>
                <div onClick={ () => SetSelectedItem(3) } className={SelectedItem === 3 ? s.itemCoosing : s.item}>2017</div>
            </div>
            <div className={props.colaps ? s.WinsBloks1 : s.WinsBloks}>
                <div className={s.Block1}>
                    <WinnersBlock Data={Data[SelectedItem][0]} name={'R-Style Lab: Базы данных (SQL)'}/>
                    <WinnersBlock Data={Data[SelectedItem][0]} name={'ISsoft: Full-stack web development'}/>
                    <WinnersBlock Data={Data[SelectedItem][0]} name={'R-Style Lab: Базы данных (SQL)'}/>
                </div>
                <div className={s.Block2}>
                    <WinnersBlock Data={Data[SelectedItem][0]} name={'R-Style Lab: Базы данных (SQL)'}/>
                    <WinnersBlock Data={Data[SelectedItem][0]} name={'ISsoft: Full-stack web development'}/>
                    <WinnersBlock Data={Data[SelectedItem][0]} name={'R-Style Lab: Базы данных (SQL)'}/>
                </div>
            </div>
        </div>
    );
}


export default Content;