import React from 'react'
import s from './QulixSystems.module.css';
import image from '../../../../../../Images/qulix.png'

const QulixSystems = () => {
    return (
        <div className={s.QulixSystems}>
            <div className={s.GeneralText}>
                <img className={s.logo} src={image}/>
                Qulix Systems  – международный поставщик услуг по разработке, тестированию ПО и IT-настоящему 
                моменту выросла до 450+ сотрудников. Мы делаем софт для финтеха, здравоохранения, образования, 
                мультимедиа; отлично разбираемся в IoT, AI, ML, а еще можем все это тестировать.
                Работа в Qulix – это: <br/>
                ·  высокотехнологичные проекты;<br/>
                ·  внутреннее обучение: семинары по hard и soft-скиллам, менторство для новичков;<br/>
                ·  корпоративные мероприятия и подарки, система скидок и бонусов для сотрудников, медицинская страховка;<br/>
                ·  совместные благотворительные проекты.<br/>
                Каждый день мы превращаем смелые и амбициозные идеи наших клиентов в 
                успешные бизнес-решения. Достичь этого нам помогает по-настоящему дружеская атмосфера, 
                легкость в общении и нацеленность на результат.
            </div>
            <div></div>
            <div></div>
        </div>
    );
}


export default QulixSystems;